.companyDetailsForm {
  padding: 5px;
  .contentPart {
    padding: 10px;
    margin: auto;
    height: 65vh;
    overflow-y: auto;
    overflow-x: hidden;
    width: 100%;
  }

  .contentPart::-webkit-scrollbar {
    width: 0.3em;
  }

  .contentPart::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(91, 90, 90, 0.2);
    border-radius: 5%;
  }

  .contentPart::-webkit-scrollbar-thumb {
    background-color: rgb(209, 204, 204);
    outline: 0.2px solid rgb(183, 186, 189);
    border-radius: 5%;
  }
  hr {
    border-top: 2px solid #e8e9e9;
  }
  .btn {
    column-gap: 17px;
  }
  .rowWidth {
    width: 270px;
  }
  .AddressWidth {
    width: 540px;
  }
  .btnWidth {
    width: 160px;
  }
}

.viewMap {
  font-size: 14px;
  font-weight: 500;
  text-decoration: none;
  line-height: 20px;
  letter-spacing: 0em;
  text-align: left;
  color: #935ed6;
}

